.tile {
  background-color: $tile-background-color;
  font-size: 1.2rem;
  color: $font-color;
  position: relative;
  border: 1px solid $border-color;
  border-radius: 5px;
  box-shadow: 0 .25rem .75rem rgba(0,0,0,.5);
  overflow: hidden;

  &-remove {
    position: absolute;
    top: 5px;
    right: 8px;
    cursor: pointer;
  }
}

.react-resizable-handle::after {
  border-right: 2px solid $font-color !important;
  border-bottom: 2px solid $font-color !important;
}
