.component-form {
  & > * {
    margin-bottom: 1.2rem;
    width: 100%;
  }
}

.color-picker {
  font-size: 14px;
  margin-bottom: 1rem;

  &-label {
    margin-right: .25rem;
  }

  &-color {
    margin-left: 1rem;
  }
}

.calendar {

  & > input {
    width:100%;
  }
}

.multiaxis-form-header {
  display: inline-block;
  font-size: 1.2rem;
  margin-left: 1rem;
}

.multiaxis-form-field {
  display: inline-block;
  margin-left: 1rem;
}

.dialgauge-band-lengths {
  padding-bottom: .5rem;
  border-bottom: 1px solid;
  margin-bottom: .5rem;
}
